<template>
  <div class="score-detail">
    <div class="header">
      <v-feed
        v-if="noticeInfo"
        :imgUrl = "getIconUrl(noticeInfo.noType)"
        :title = "noticeInfo.noTitle"
        :subtitle = "getPublisher(publisher)"
        :time = "getTime(noticeInfo.noReceipt,noticeInfo.noReceiptTime)"
        :receiptInfo="getReceiptInfo(noticeInfo.noReceipt,noticeInfo.noReceiptTime,noticeInfo.nrReceipt)"
        :showRelation="true"
        :myFeedId="myFeedId"
        :friendFeedId="friendFeedId"
      > </v-feed>
    </div>
    <div class="table">
      <div v-if="scoreList.length > 0">
      <div class="head">
        <p>{{stuName}}</p><span class="cla">{{className}}</span><span class="cla mar-left" v-if="noticeInfo.noRanking === 1">班级排名：{{scoreRank}}</span>
      </div>
      <table>
        <thead>
          <th>学科</th>
          <th>成绩</th>
          <th>班级平均分</th>
        </thead>
        <tbody>
        <tr v-for="(item,index) in scoreList" :class="{'even': index %2 !== 0}">
          <td>{{item.subject}}</td>
          <td>{{item.score}}</td>
          <td>{{item.avgscore}}</td>
        </tr>
        </tbody>
        <em></em>
      </table>
      </div>
    </div>
    <div class="footer-btn" @click="goHistory" v-if="showBtnFirst">
      <button :class="{'btn': confirmBtn}">查看历史成绩</button>
    </div>
    <div class="tBtn" v-if="showBtnSecond">
        <span class="w165" @click="goHistory">查看历史成绩</span>
        <span class="receiptBtn" v-text="btnDefault" :class="{'gray': showGray}" @click="receiptClick"></span>
    </div>
    <!--发布成功弹框-->
    <div v-show="showToast" class="shadow">
      <span></span>
      <p>已回执</p>
    </div>
    <!--已超时-->
    <div class="timeout" v-show="showTimeout">
      <span></span>
      <p>已超时</p>
    </div>
  </div>
</template>

<script>
  import vFeed from '../vendor/v-feed';
  import api from '../utils/api';
  export default {
    name: 'score-detail',
    components: {'v-feed': vFeed},
    data () {
      return {
        confirmBtn: false,
        scoreList: [], // 成绩列表
        noticeInfo: {}, // 名片信息
        publisher: '', // 发布人
        className: '', // 班级信息
        scoreRank: '', // 班级排名
        stuName: '', // 学生姓名
        showBtnFirst: false,
        showBtnSecond: false,
        btnDefault: '回执',
        showGray: false,
        showToast: false, // 是否展示toast
        showTimeout: false,
        myFeedId: '', // 我的feedId
        friendFeedId: '' // 发布人的feedId
      };
    },
    created () {
      this.getData();
    },
    methods: {
      getData () {
        let params = {
          noticeId: this.$route.query.noId
        };
        api.getScoreDetail(params).then((res) => {
          if (res && res.data && res.data.data) {
            const data = res.data.data;
            this.scoreList = data.scoreList;
            this.noticeInfo = data.noticeInfo;
            this.publisher = data.publisher;
            this.className = data.className;
            this.scoreRank = data.scoreRank;
            this.stuName = data.stuName;
            // 获取我的feedId
            this.myFeedId = data.currentUserFeedId;
            // 获取发布人feedId
            this.friendFeedId = data.publisherFeedId;
          }
        }, (err) => {
          console.log(err);
        });
      },
      goHistory () {
        this.$router.push({path: '/historyScore', query: {examId: this.noticeInfo.noExamId}});
      },
      // 获取回执时间
      getTime (noReceipt, noReceiptTime) {
        let [time] = [0];
        if (noReceipt === 0) {
          time = 0;
        } else if (noReceipt === 1 && noReceiptTime && noReceiptTime !== 253402271639000) {
          time = noReceiptTime;
        } else if (noReceipt === 1 && noReceiptTime && noReceiptTime === 253402271639000) {
          time = 0;
        }
        return {time};
      },
      /**
       * 获取头像url 类别
       * @param type
       */
      getIconUrl (type) {
        let url = '';
        if (type === 0) {
          url = require('../assets/icon-tz.png');
        } else if (type === 1) {
          url = require('../assets/icon-zy.png');
        } else {
          url = require('../assets/icon-cj.png');
        }
        return url;
      },
      /**
       * 获取当前发布人
       * @param publisher
       * @returns {string}
       */
      getPublisher (publisher) {
        return '发布人：' + publisher;
      },
      /**
       * 判断是否需要显示回执状态 （根据回执状态 及回执时间判断）
       * @param receipt
       * @param receiptTime
       */
      getReceiptInfo (receipt, receiptTime, nrReceipt) {
        let [flag, receiptImg, setTimeColor] = ['', '', false];
        // 获取当前系统时间戳
        var timestamp = (new Date()).valueOf();
        if (receipt === 0) {
          flag = '-1';
          receiptImg = '';
          this.showBtnFirst = true;
          this.showBtnSecond = false;
          this.btnDefault = '';
          this.showGray = false;
          setTimeColor = false;
        } else if (receipt === 1 && nrReceipt === 0 && !receiptTime) {
          flag = '0';
          receiptImg = require('../assets/icon-xhz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '回执';
          this.showGray = false;
          setTimeColor = false;
        } else if (receipt === 1 && nrReceipt === 0 && receiptTime === 253402271639000) {
          flag = '0';
          receiptImg = require('../assets/icon-xhz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '回执';
          this.showGray = false;
          setTimeColor = false;
        } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime === 253402271639000) {
          flag = '0';
          receiptImg = require('../assets/icon-yhz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '已回执';
          this.showGray = true;
          setTimeColor = true;
        } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime !== 253402271639000) {
          flag = '0';
          receiptImg = require('../assets/icon-yhz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '已回执';
          this.showGray = true;
          setTimeColor = true;
        } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime > timestamp) {
          flag = '1';
          receiptImg = require('../assets/icon-xshz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '回执';
          this.showGray = false;
          setTimeColor = false;
        } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime < timestamp) {
          flag = '0';
          receiptImg = require('../assets/icon-yjz.png');
          this.showBtnFirst = false;
          this.showBtnSecond = true;
          this.btnDefault = '回执';
          this.showGray = true;
          setTimeColor = true;
        }
        return {flag, receiptImg, setTimeColor};
      },
      /**
       * 点击回执
       */
      receiptClick () {
        // 获取当前系统时间戳
        let timestamp = (new Date()).valueOf();
        let params = {
          noticeId: this.$route.query.noId
        };
        if (!this.showGray) {
          if (!this.noticeInfo.noReceiptTime || this.noticeInfo.noReceiptTime === 253402271639000 || (this.noticeInfo.noReceiptTime !== 253402271639000 && this.noticeInfo.noReceiptTime > timestamp)) {
            api.receiptNotice(params).then((res) => {
              console.log(res);
              this.getData();
              this.successStatus();
            }, (err) => {
              console.log(err);
            });
          } else {
            this.getData();
            this.timeOutStatus();
          }
        }
      },
      /**
       * 回执成功后更改状态
       */
      successStatus () {
        this.btnDefault = '已回执';
        this.showGray = true;
        this.showToast = true;
        setTimeout(() => {
          this.showToast = false;
        }, 500);
      },
      /**
       * 已超时状态
       */
      timeOutStatus () {
        this.btnDefault = '回执';
        this.showGray = true;
        this.showTimeout = true;
        setTimeout(() => {
          this.showTimeout = false;
        }, 500);
      }
    }
  };
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
  body{
    background:#f2f2f4;
  }
  .mar-left{
    margin-left: 15px;
  }
  .score-detail{
    width:100%;
    height:auto;
    background:#f2f2f4;
    .header{
      width: 100%;
      /*height: 89px;*/
      /*border-bottom:1px solid #e1e1e1;*/
    }
    /*.v-feed-info.four-word{*/
      /*display:none;*/
    /*}*/
    .v-feed{
      border-bottom: none;
      background: #fff;
      &::after{
         border-bottom:0 none;
       }
    }
    .v-feed__num{
      display: none;
    }
    .v-feed__con{
      top: pxTorem(2px);
    }
    /*覆盖 feed 组件样式*/
    .v-feed{
      .v-feed__img{
        margin-top: -4px;
      }
    }
    .table{
      margin: 10px 15px 51px 15px;
      background:#fff;
      position: relative;
      width:345px;
      /*height:399px;*/
      padding:30px 21px 30px 21px;
      border-radius: 10px;
      thead{
        background: #FFECED;
        th{
          width: 101px;
          height:30px;
          line-height:30px;
          font-size: 14px;
          color: #C3303A;
        }
      }
      tbody{
        tr{
          font-size: 14px;
          color: #383838;
          text-align: center;
          td{
            text-align: center;
            width: 101px;
            height:30px;
            line-height:30px;
            font-size: 14px;
            color: #383838;
          }
        }
      }
      .head{
        margin-bottom: 19px;

        p{
          display: inline-block;
          font-size: 20px;
          color: #383838;
          line-height: 20px;
          margin-right: 20px;
        }
        .cla{
          font-size: 15px;
          color: #9C9C9C;
          line-height: 17px;
        }
      }
    }
    em{
      position: absolute;
      bottom:0;
      right: 0;
      display: inline-block;
      width:49px;
      height:45px;
      background:url('../assets/group.png') no-repeat center;
      background-size:100%;
    }
    .tBtn{
      width: 100%;
      height: 54px;
      box-sizing: border-box;
      background: #FFFFFF;
      position: fixed;
      left: 0;
      bottom: 0;
      overflow: hidden;
      border-top: 1px solid #DDDEE3;/*no*/
      padding:8px 15px;
      background: rgba(255,255,255,0.90);
      box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
      span{
        border-radius: 4px;
        display: inline-block;
        width: 165px;
        font-size: 17px!important;
        line-height: 38px;
        text-align: center;
        color: #FFFFFF;
        background: #C3303A;
      }
      span:nth-child(2){
        margin-left: 10px;
      }
      button {
        width: 315px;
        height: 38px;
        font-size: 17px!important;
        line-height: 38px;
        text-align: center;
        color: #FFFFFF;
        background: #C3303A;
        border-radius: pxTorem(4px);
      }
      .receiptBtn{
        &.gray{
          background: #DDDEE3;
        }
      }
    }
    .footer-btn {
      width: 100%;
      height: 54px;
      padding: 8px 30px;
      box-sizing: border-box;
      background: #FFFFFF;
      position: fixed;
      left: 0;
      bottom: 0;
      overflow: hidden;
      border-top: 1px solid #DDDEE3;/*no*/
      background: rgba(255,255,255,0.90);
      box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
      span{
        display: inline-block;
        width: 165px;
      }
      button {
        width: 315px;
        height: 38px;
        font-size: 17px!important;
        line-height: 38px;
        text-align: center;
        color: #FFFFFF;
        background: #C3303A;
        border-radius: 4px;
      }

    }
    .even{
      background: #F9F9F9;
    }
    /*toast 提示*/
    .shadow{
      width: 120px;
      height: 120px;
      background:#000000;
      opacity: 0.5;
      border-radius:5px;
      margin: -305px auto;
      span{
        margin:15px 0 0 35px;
        /*padding:15px 35px 0 35px;*/
        display: inline-block;
        width:50px;
        height:50px;
        background:url('../assets/toast-rig.png') no-repeat center;
        background-size:100%;
      }
      p{
        margin-left:35px;
        font-size: 13px;
        color: #FFFFFF;
        letter-spacing: 0;
      }
    }
    /*toast 提示*/
    .timeout{
      width: 120px;
      height: 120px;
      background:#000000;
      opacity: 0.5;
      border-radius:5px;
      margin: -305px auto;
      span{
        margin:15px 0 0 35px;
        /*padding:15px 35px 0 35px;*/
        display: inline-block;
        width:50px;
        height:50px;
        background:url('../assets/toast-rig.png') no-repeat center;
        background-size:100%;
      }
      p{
        margin-left:35px;
        font-size: 13px;
        color: #FFFFFF;
        letter-spacing: 0;
      }
    }
  }
</style>



